<template>
  <div style="padding-top:60px;">
  <!-- 列表頁 -->
  <div class="container main-contant mb-1" style="min-height:700px;">
    <loading :active.sync="isLoading"></loading>
    <div class="row">
      <div class="col-md-3">
        <!-- 左側選單 (List group) -->
        <div class="list-group sticky-top">
          <a class="list-group-item list-group-item-action" :class="{'active': selected==''}" href="#"  @click.prevent="selected=''; getProducts(1)">
            <i class="fas fa-tag" aria-hidden="true"></i>所有商品</a>
          <a class="list-group-item list-group-item-action" :class="{'active': selected=='glasses'}" href="#"  @click.prevent="selected='glasses'; getProducts('all')">
            <i class="fas fa-glass-cheers" aria-hidden="true"></i>婚紗酒杯</a>
          <a class="list-group-item list-group-item-action" :class="{'active': selected=='deco'}" href="#"  @click.prevent="selected='deco'; getProducts('all')">
            <i class="fa fa-splotch" aria-hidden="true" ></i>婚禮布置</a>
          <a class="list-group-item list-group-item-action" :class="{'active': selected=='flower'}" href="#"  @click.prevent="selected='flower'; getProducts('all')">
            <i class="fa fa-leaf" aria-hidden="true"></i> 新娘捧花</a>
          <a class="list-group-item list-group-item-action" :class="{'active': selected=='other'}" href="#"  @click.prevent="selected='other'; getProducts('all')">
            <i class="fa fa-gift" aria-hidden="true"></i> 其他用品</a>
        </div>
      </div>
      <div class="col-md-9">
        <div class="d-flex mb-4">
          <!-- Search bar -->
          <form class="form-inline my-3 my-lg-0">
            <div class="input-group">
              <input class="form-control" type="text" placeholder="Search" aria-label="Search" v-model="serchTxt">
              <div class="input-group-btn">
                <button class="btn btn-outline-warning" type="submit" disabled>
                  <i class="fa fa-search" aria-hidden="true"></i> 搜尋產品</button>
              </div>
            </div>
          </form>
        </div>
        <!-- 主要商品列表 (Card) -->
        <div class="tab-content">
          <!--產品分類-->
          <div class="tab-pane active">
            <div class="row">
              <!-- 婚紗酒杯 -->
              <div class="col-md-4 mb-4 productItem" v-for="(item,index) in filtData" :key="index"  @click="productDetail(item.id)">
                <div class="card border-0 box-shadow text-center h-100">
                  <div class="productImg" style="height: 300px; background-size: cover; background-position: center"
                    :style="{backgroundImage: `url(${item.imageUrl})`}">
                  </div>
                  <div class="card-body">
                    <h4 class="card-title">{{ item.title }}</h4>
                    <p class="card-text">{{ item.description }}</p>
                  </div>
                  <div class="card-footer border-top-0 bg-white" style="padding:0;">
                    <a href="#" class="btn btn-primary btn-sm btn-block">
                      see more
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <!-- pagination 只有點選所有商品時才分頁all-->
            <!-- <Pagination :pages="pagination" @emitPages="getProducts"  v-if="selected==''"></Pagination> -->
          </div>
        </div>
        <!-- tab-content end -->

      </div>

    </div>
  </div>
  <!-- 列表頁 -->    
  </div>
</template>
<script>
import $ from 'jquery';
import Pagination from '../Pagination';
export default {
  components: {
    Pagination
  },
  data() {
    return {
      serchTxt: '',
      selected: '',
      allPage: false,
      products: [],
      pagination:{},
      cartList: [],
      product: {},
      isLoading: false,
      status: {
        loadingItem: '',
      },
    };
  },
  methods: {
    getProducts(page = 1) {
      const vm = this;
      let url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products/all`;
      // if (page == 'all') {
			// 	url += `/all`;
			// 	vm.allPage = true;
			//   } else {
			// 	url += `?page=${page}`;
			// 	vm.allPage = false;
			// }
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        console.log(response.data.products)
        vm.products = response.data.products;
        vm.isLoading = false;
        if (page !== 'all') { 
        	vm.pagination = response.data.pagination
        } else {
        	vm.products = vm.products.reverse()
        }
      });
    },
    addtoCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty,
      };
      this.$http.post(url, { data: cart }).then((response) => {
        vm.status.loadingItem = '';
        vm.getCart();
      });
    },
    getCart() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.cartList = response.data.data;
        vm.isLoading = false;
      });
    },
    productDetail(id){
      this.$router.push({
				name: 'PrdDetail',
				params: { productId: id },
			});    
    }
  },
  created() {
    this.getProducts();
    this.getCart();
  },	
  computed: {
		filtData() {
			var vm = this;
			if (vm.selected=='' && vm.serchTxt==''){
				return vm.products;
			} else if (vm.selected!=='' && vm.serchTxt==''){
				return vm.products.filter(function(item) {
					return item.category == vm.selected;
				})
			} else {
        return vm.products.filter(function(item){
				  return item.title.toLowerCase().includes(vm.serchTxt.toLowerCase());
				})
      }
		}
	},
};
</script>
<style scoped>
.productItem {
  cursor: pointer;
}
.productItem .productImg{
	opacity: 1;
	-webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
}
.productItem:hover .productImg{
	opacity: .5;
}
</style>


