import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/admin/Dashboard'
import login from '@/admin/pages/login'
import Products from '@/admin/pages/Products'
import Coupons from '@/admin/pages/Coupons'
import Orders from '@/admin/pages/Orders'
import customerOrder from '@/admin/pages/customerOrder'
import CustomerCheckout from '@/admin/pages/CustomerCheckout'
import layout from '@/components/layout'
import home from '@/components/home'
import PrdList from '@/components/pages/products'
import about from '@/components/pages/about'
import coupon from '@/components/pages/coupon'
import PrdDetail from '@/components/pages/product_detail'
import checkout from '@/components/pages/checkout'
import paymentSuccess from '@/components/pages/payment_success'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/'
    },
    {
      path: '/',
      component: layout,
      children: [
        {
          path: '',
          name: 'home',
          component: home
        },
        {
          path: '/about',
          name: 'about',
          component: about
        },
        {
          path: '/coupon',
          name: 'coupon',
          component: coupon
        },
        {
          path: '/products_list',
          name: 'products_list',
          component: PrdList
        },
        {
          path: '/PrdDetail/:productId',
          name: 'PrdDetail',
          component: PrdDetail
        },
        {
          path: '/checkout',
          name: 'checkout',
          component: checkout
        },
        {
          path: '/payment_success',
          name: 'payment_success',
          component: paymentSuccess
        }
      ]
    },

    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/admin',
      name: 'Dashboard',
      component: Dashboard,
      children: [
        {
          path: 'products',
          name: 'Products',
          component: Products,
          meta: { requiresAuth: true }
        },
        {
          path: 'coupons',
          name: 'Coupons',
          component: Coupons,
          meta: { requiresAuth: true }
        },
        {
          path: 'orders',
          name: 'Orders',
          component: Orders,
          meta: { requiresAuth: true }
        }
      ]
    },
    {
      path: '/',
      name: 'fake_order',
      component: Dashboard,
      children: [
        {
          path: 'customer_order',
          name: 'customerOrder',
          component: customerOrder
        },
        {
          path: 'customer_checkout/:orderId',
          name: 'CustomerCheckout',
          component: CustomerCheckout
        }
      ]
    }
  ]
})



