<template>
  <div>
    <nav class="col-md-2 d-none d-md-block bg-light sidebar">
      <div class="sidebar-sticky">
        <h6
          class="sidebar-heading d-flex justify-content-between align-items-center px-3 mt-4 mb-1 text-muted"
        >
          <span><i class="fas fa-user-check"></i>管理員</span>
          <a class="d-flex align-items-center text-muted" href="#">
            <span data-feather="plus-circle"></span>
          </a>
        </h6>
        <ul class="nav flex-column mb-2">
          <li class="nav-item">
            <router-link class="nav-link" :class="{'active' : router_name === 'Products'}" to="/admin/products"><i class="fas fa-box-open"></i>產品列表</router-link>
            <router-link class="nav-link" :class="{'active' : router_name === 'Orders'}" to="/admin/orders"><i class="fas fa-clipboard-list"></i>訂單列表</router-link>
            <router-link class="nav-link" :class="{'active' : router_name === 'Coupons'}" to="/admin/coupons"><i class="fas fa-ticket-alt"></i>優惠券管理</router-link>
          </li>
        </ul>
        <h6
          class="sidebar-heading d-flex justify-content-between align-items-center px-3 mt-4 mb-1 text-muted"
        >
          <span><i class="fas fa-user-check"></i>模擬功能</span>
          <a class="d-flex align-items-center text-muted" href="#">
            <span data-feather="plus-circle"></span>
          </a>
        </h6>
        <ul class="nav flex-column mb-2">
          <li class="nav-item">
            <router-link class="nav-link" :class="{'active' : router_name === 'customerOrder'}"  to="/customer_order"><i class="fas fa-cart-arrow-down"></i>模擬訂單</router-link>
          </li>
        </ul>
      </div>
    </nav>
  </div>
</template>
<script>
export default {
  data() {
    return {
      router_name: '',
    }
  },
  created() {
    this.router_name = this.$route.name;
  },
  watch: {
    '$route' (to, from) {
      // react to route changes...
      this.router_name = this.$route.name;
    }
  }
}
</script>



