<template>
  <div style="padding-top:60px;">
  <!-- 產品頁面 -->
  <loading :active.sync="isLoading"></loading>
  <div class="container main-contant mb-1">
    <nav aria-label="breadcrumb" role="navigation">
      <ol class="breadcrumb bg-transparent pl-0">
		  	<div class="container d-flex">
		  		<li class="breadcrumb-item"><router-link to="/">Wedding Easy</router-link></li>
			    <li class="breadcrumb-item">{{product.category}}系列</li>
			    <li class="breadcrumb-item active" aria-current="page">{{product.title}}</li>
		  	</div>
      </ol>
    </nav>
    <div class="row">
      <div class="col-md-4 mb-5">
        <div class="sticky-top" style="top: 10px;">
          <h1 class="h2">{{ product.category }}
          </h1>
          <div class="d-flex my-3 align-items-end justify-content-end">
            <del class="text-muted"> {{ Number(product.origin_price) | toCurrency }} 元</del>
            <div class="h4 mb-0 ml-auto text-danger">
              <small>特價 NT$</small>
              <strong>{{ Number(product.price) | toCurrency }} 元</strong>
            </div>
          </div>
          <hr>

          <div class="input-group mt-3">
            <select name="" class="form-control mr-1" v-model="product.num">
              <option value="0" selected disabled>
                選擇數量
              </option>
              <option :value="num" v-for="num in 15" :key="num">
                選購 {{num}} {{product.unit}}
              </option>
            </select>
             <a href="#" class="btn btn-primary" @click.prevent="addtoCart(product.id, product.num)">
              <i class="fa fa-cart-plus" aria-hidden="true" ></i> 加入購物車
            </a>
          </div>

          <div class="mt-2 text-right text-muted">
           <i class="fab fa-cc-visa fa-2x" aria-hidden="true"></i>
            <i class="fab fa-cc-jcb fa-2x" aria-hidden="true"></i>
            <i class="fab fa-cc-paypal fa-2x" aria-hidden="true"></i>
            <i class="fab fa-cc-mastercard fa-2x" aria-hidden="true"></i>
          </div>

        </div>


      </div>
      <div class="col-md-8">
        <h2>{{ product.title}}</h2>
        <p class="card-text">{{ product.description }} </p>
        <h5>相關須知</h5>
        <p class="card-text">{{ product.content }} </p>
        <hr>
        <img :src="product.imageUrl" class="w-100" alt="">

        <div class="alert alert-secondary mt-4" role="alert">
          <h2 class="text-center">購物說明</h2>
          <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Veniam, at! Totam, exercitationem repellat. Saepe facere
            amet expedita perferendis voluptatem dicta dignissimos tempora ut atque, rerum doloribus? Magni, blanditiis.
            Assumenda, distinctio!</p>
        </div>
        <div class="text-right">
						<a class="btn btn-primary text-white mt-2" href="#"  @click.prevent="goback()">Back</a>
				</div>
      </div>

    </div>
  </div>
  <!-- 產品頁面 -->
  </div>
</template>
<script>
export default {
	data () {
		return {
			isLoading: false,
			productId: '',
			product: {},
		}
  },
  methods:{
    getProduct(id) {
      const vm = this;
      vm.isLoading = true;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      this.$http.get(url).then((response) => {
        vm.product = response.data.product;
        vm.isLoading = false;
        console.log(response.data.product)
      });
    },
    checkRouterName() {
      if ((this.$route.name).indexOf('PrdDetail') >= 0) {
        this.getProduct(this.$route.params.productId);
      }
    },
    goback() {
      this.$router.back();
    },
    addtoCart(id, qty = 1){
      this.$store.dispatch('addtoCart', {id, qty});
    },
  },
  watch: { 
     '$route.name': {
        handler: function() {
           this.productId = this.$route.params.productId;
           this.checkRouterName();
        },
        deep: true,
        immediate: true
      }
    },
}
</script>


