<template>
  <div>
    <loading :active.sync="isLoading"></loading>
  <!-- headers -->
  <nav class="navbar navbar-expand-lg fixed-top navbar-light bg-light">

    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <!-- nav 左邊-->
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav mr-auto">
        <li class="nav-item">
          <router-link class="nav-link" :class="{'active' : router_name === 'home'}"  to="/">
            <i class="fa fa-heart text-danger" aria-hidden="true"></i>
              Wedding Easy
          </router-link>
        </li>
        <!-- <li class="nav-item">
          <router-link class="nav-link" :class="{'active' : router_name === 'about'}"  to="/about">關於我</router-link>
        </li> -->
        <li class="nav-item">
          <router-link class="nav-link" :class="{'active' : router_name === 'products_list'}"  to="/products_list">商品區</router-link>
        </li>
        <li class="nav-item dropdown">
          <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            其他資訊
          </a>
          <div class="dropdown-menu" aria-labelledby="navbarDropdown">
            <a class="dropdown-item" href="#">沒有東西</a>
            <a class="dropdown-item" href="#">婚紗酒杯</a>
            <a class="dropdown-item" href="#">婚禮小物</a>
            <div class="dropdown-divider"></div>
            <a class="dropdown-item" href="#">訂/結婚鑽戒</a>
            <a class="dropdown-item" href="#">婚禮場地</a>
            <a class="dropdown-item" href="#">婚禮布置</a>
          </div>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" :class="{'active' : router_name === 'coupon'}"  to="/coupon">COUPON</router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" :class="{'active' : router_name === 'login'}"  to="/login">後台登入</router-link>
        </li>
      </ul>
    </div>
    <!-- 購物車內的數量 (Button 內包含 icon, 數量 badge) -->
    <div class="dropdown ml-auto">
      <button class="btn btn-sm btn-cart" data-toggle="dropdown" data-flip="false">
        <i class="fa fa-shopping-cart  fa-2x" aria-hidden="true"></i>
        <span class="badge badge-pill badge-primary">{{cart.carts.length}}</span><!--購物車物品總數-->
        <span class="sr-only">unread messages</span>
      </button>
      <div class="dropdown-menu dropdown-menu-right p-3" style="min-width: 300px" data-offset="400">
        <h6>已選購的商品</h6>
        <table class="table table-sm">
          <tbody>
            <tr v-if="cart.carts.length==0">購物車還沒有東西耶!</tr>
            <tr v-for="(item, key) in cart.carts" :key="key">
              <td class="align-middle text-center">
                <a href="#removeModal" class="text-muted" @click="removeCart(item.id)">
                  <i class="fa fa-trash-alt" aria-hidden="true"></i>
                </a>
              </td>
              <td class="align-middle">{{ item.product.title }}</td>
              <td class="align-middle">{{ item.qty }}{{ item.product.unit }}</td>
              <td class="align-middle text-right">{{item.final_total | toCurrency}}</td>
            </tr>
          </tbody>
        </table>
        <div>
        <router-link  v-if="cart.carts.length!==0"  to="/checkout" class="btn btn-primary btn-block">
          <i class="fa fa-cart-plus" aria-hidden="true"></i> 結帳去
        </router-link>
        <a v-else class="btn btn-primary btn-block"  href="#" >
          <i class="fa fa-cart-plus" aria-hidden="true"></i> 繼續購物
        </a>
        </div>
      </div>
    </div>
  </nav>
  <!--nav結束-->
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';
import $ from 'jquery';
export default {
  data() {
    return {
      router_name: '',
      isShow: false,
    }
  },
  methods: {
    ...mapActions(['getCart']),
    removeCart(id) {
      this.$store.dispatch('removeCart', id);
    }
  },
  created() {
    this.router_name = this.$route.name;
  },  
  computed: {
    ...mapGetters(['cart', 'isLoading'])
  },
	created() {
		this.getCart();
	},
  watch: {
    '$route' (to, from) {
      // react to route changes...
      this.router_name = this.$route.name;
    }
  }
}
</script>


