<template>
  <div style="padding-top:60px;">
  <loading :active.sync="isLoading"></loading>
  <!-- 結帳流程 -->
  <div class="container main-contant py-5">
    <h1 class="text-center mb-3 text-secondary">購物車結帳</h1>
    <section class="form-row align-items-center text-center">
      <div class="col">
        <div class="alert alert-rounded mb-0" :class="{'alert-success': step===1 , ' alert-light': step!==1}" role="alert">
          <i class="fas fa-shopping-cart"></i> 商品確認
        </div>
      </div>
      <div class="col">
        <div class="alert alert-rounded mb-0" :class="{'alert-success': step===2 , ' alert-light': step!==2}"  role="alert">
          <i class="fas fa-edit"></i> 訂購資訊
        </div>
      </div>
      <div class="col">
        <div class="alert alert-rounded mb-0" :class="{'alert-success': step===3 , ' alert-light': step!==3}" role="alert">
          <i class="far fa-credit-card"></i> 訂單完成
        </div>
      </div>
    </section>
    <!--購物車裡的品項確認 step1-->
    <section v-if="step==1">
    <div class="row justify-content-md-center" v-if="cart.total">
      <div class="col-md-12">
        <table class="table mt-4" >
          <thead>
            <tr>
              <th scope="col">
                <div class="delAll text-danger" @click="removeAllCart()">
				    		  <i class="far fa-trash-alt mr-1"></i>
				    		  <b class="d-none d-md-inline-block">清空購物車</b>
				    	  </div>
              </th>
              <th scope="col">品項</th>
              <th class="text-right" scope="col">數量</th>
              <th class="text-right" scope="col">單價</th>
              <th class="text-right" scope="col">總價</th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="cartEmpty">購物車還沒有東西哦！</tr>
            <tr v-for="(item, index) in cart.carts" :key="index">
              <td class="align-middle">
                <button
                  class="btn btn-outline-danger btn-sm"
                  @click="removeCart(item.id)"
                ><i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                {{ item.product.title }}
                <div class="text-success" v-if="item.coupon">
                  已套用優惠券 - {{ item.coupon.title}}
                </div>
              </td>
              <td  class="text-right">{{ item.qty }}</td>
              <td  class="text-right">{{ Number(item.product.price) | toCurrency }}</td>
              <td  class="text-right">{{ Number(item.final_total) | toCurrency }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="4" class="text-right">總計</td>
              <td class="text-right">{{ cart.total | toCurrency }}</td>
            </tr>
            <tr v-if="cart.final_total !== cart.total">
              <td colspan="4" class="text-right text-success">折扣價</td>
              <td class="text-right text-success">{{ cart.final_total | toCurrency }}</td>
            </tr>
          </tfoot>
        </table>
        <div class="input-group mb-3 input-group-md">
          <input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼">
          <div class="input-group-append">
            <button class="btn btn-outline-secondary" type="button" @click="addCoupon">
              套用優惠碼
            </button>
          </div>
        </div>
      </div>
    </div>
    </section>
    <!--訂購人資料輸入 step2-->
    <section class="row justify-content-center mt-5"  v-if="step==2">
      <div class="col-md-8">
        <div class="card">
          <div class="card-header" id="headingOne">
            <h6 class="mb-0 d-flex align-items-center">
              <a data-toggle="collapse" href="#collapseOne">
                顯示購物車細節
                <i class="fa fa-angle-down" aria-hidden="true"></i>
              </a>
              <span  class="h3 ml-auto mb-0" v-if="cart.final_total !== cart.total">
                {{ cart.final_total | toCurrency }}
              </span>
              <span class="h3 ml-auto mb-0" v-else>
                {{ cart.total| toCurrency }}
              </span>
            </h6>
          </div>
        </div>
        <div id="collapseOne" class="collapse mt-3">
          <table class="table table-sm">
            <thead>
							<th>商品</th>
              <th>名稱</th>
							<th width=60>數量</th>
						  <th min-width=60  class="text-right" v-if="cart.final_total!==cart.total">折扣價</th>
							<th min-width=60 text-right v-else class="text-right">售價</th>
						</thead>
						<tbody>
						  <tr v-for="(item, key) in cart.carts" :key="key">
                <td class="align-middle">
                  <img :src=" item.product.imageUrl" class="img-fluid img-thumbnail" alt="">
                </td>
								<td class="align-middle">{{ item.product.title }}</td>
								<td class="align-middle">{{ item.qty }}{{ item.product.unit }}</td>
								<td class="align-middle text-right">{{ item.final_total | toCurrency }}</td>
							</tr>
						</tbody>
						<tfoot>
							<tr class="text-danger">
								<td colspan="3" class="text-right">
									<span v-if="cart.final_total==cart.total">總計金額</span>
									<span v-else>折扣後金額</span>
								</td>
								<td class="text-right">{{ cart.final_total | toCurrency }}</td>
							</tr>
						</tfoot>
          </table>
        </div>
      </div>
      <div class="col-md-8"><ValidationObserver ref="observer" tag="form" @submit.prevent="createOrder">
        <div class="form-group">
          <validation-provider rules="required|email" v-slot="{ errors }" name="email">
            <label for="useremail">Email</label>
            <input v-model="form.user.email" type="email" class="form-control" name="email" id="useremail" placeholder="請輸入 Email"/>
            <span class="text-danger">{{ errors[0] }}</span>
          </validation-provider>
        </div>
        <div class="form-group">
          <validation-provider rules="required" v-slot="{ errors }" name="name">
            <label for="username">收件人姓名</label>
            <input v-model="form.user.name" type="text" class="form-control" name="name" id="username" placeholder="請輸入 收件人姓名"/>
            <span class="text-danger">{{ errors[0] }}</span>
          </validation-provider>
        </div>
        <div class="form-group">
          <validation-provider rules="required" v-slot="{ errors }" name="tel">
            <label for="usertel">收件人電話</label>
            <input v-model="form.user.tel" type="text" class="form-control" name="tel" id="usertel" placeholder="請輸入 電話"/>
            <span class="text-danger">{{ errors[0] }}</span>
          </validation-provider>
        </div>
        <div class="form-group">
          <validation-provider rules="required" v-slot="{ errors }" name="address">
            <label for="useraddress">收件人地址</label>
            <input v-model="form.user.address" type="text" class="form-control" name="address" id="useraddress" placeholder="請輸入 收件地址"/>
            <span  class="text-danger">{{ errors[0] }}</span>
          </validation-provider>
        </div>
        <div class="form-group">
          <label for="useraddress">留言</label>
          <textarea name="" id="" class="form-control" cols="20" rows="5"
            v-model="form.message"></textarea>
        </div>
      </ValidationObserver>
      </div>
    </section>

    <!--訂單完成 step3-->
    <section class="row justify-content-center"  v-if="step==3">
      <div class="col-md-12">
        <h5 class="py-3 mb-2 text-center bg-light">
          訂單完成
        </h5>
        <div class="row justify-content-center">
          <form class="col-md-8" @submit.prevent="payOrder">
            <table class="table">
              <thead>
                <th>品名</th>
                <th>數量</th>
                <th>單價</th>
              </thead>
              <tbody>
                <tr v-for="item in order.products" :key="item.id">
                  <td class="align-middle">{{ item.product.title }}</td>
                  <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                  <td class="align-middle text-right">{{ item.final_total }}</td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td colspan="2" class="text-right">總計</td>
                  <td class="text-right">{{ order.total }}</td>
                </tr>
              </tfoot>
            </table>
            <table class="table">
              <tbody>
                <tr>
                  <th>訂單編號</th>
                  <td>{{ orderId }}</td>
                </tr>
                <tr>
                  <th>Email</th>
                  <td>{{ order.user.email }}</td>
                </tr>
                <tr>
                  <th>姓名</th>
                  <td>{{ order.user.name }}</td>
                </tr>
                <tr>
                  <th>收件人電話</th>
                  <td v-if="order.user.tel==''">未填寫</td>
                  <td v-else>{{ order.user.tel }}</td>
                </tr>
                <tr>
                  <th>收件人地址</th>
                  <td>{{ order.user.address }}</td>
                </tr>
                <tr>
                  <th>付款狀態</th>
                  <td>
                    <span v-if="!order.is_paid">尚未付款</span>
                    <span v-else class="text-success">付款完成</span>
                  </td>
                </tr>
              </tbody>
            </table>
            <div class="text-right" v-if="order.is_paid === false">
              <button class="btn btn-danger">付款去</button>
            </div>
          </form>
        </div>
      </div>  
    </section>
		<!-- button -->
		<div class="row justify-content-center my-4">
			
			<div class="col-md-8 order-md-2 u-orderbtn-step" v-if="cart.total!==0">
				<button type="button" class="btn btn-primary d-block mr-2" v-if="step==2" @click="stepChange('prev')">
					上一步
				</button>
				<button type="button" class="btn btn-primary d-block" v-if="step==1" @click="stepChange()">
					下一步：填寫收件資料
				</button>
				<button type="button" class="btn btn-success d-block" v-if="step==2" @click="createOrder()">
					下一步：完成訂單
				</button>
			</div>
			<div :class="{'col-md-4':cart.total!==0}" class="order-md-1 u-orderbtn-shop">
				<router-link class="nav-item btn btn-outline-primary d-inline-block" to="/products_list"> 繼續購物</router-link>
			</div>
		</div>
  </div>

  <!-- 輸入資料頁 -->
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';
import $ from 'jquery';
import { ValidationProvider, extend, ValidationObserver } from 'vee-validate';
import * as rules from 'vee-validate/dist/rules';
import zh_TW from 'vee-validate/dist/locale/zh_TW';
// loop over all rules
for (let rule in rules) {
  extend(rule, {
    ...rules[rule], // add the rule
    message: zh_TW.messages[rule] // add its message
  });
}
export default {
  components: {
    ValidationProvider,
    ValidationObserver
  },
	data () {
		return {
			step: 1,
			coupon_code: '',
			form: {
      	user: {
      		name: '',
      		email: '',
      		tel: '',
      		address: '',
      	},
      	message: '',
      },
      orderId:'',
			order: {
				user: {
      		name: '',
      		email: '',
      		tel: '',
      		address: '',
      	}
			},
		}
  },
	methods: {
    ...mapActions(['getCart', 'removeAllCart']),
    removeCart(id) {
      this.$store.dispatch('removeCart', id);
    },
    addCoupon() {
      this.$store.dispatch('addCoupon', this.coupon_code);
    },
    stepChange(dir) {
    	const vm = this;
    	document.body.scrollTop = 0;
    	document.documentElement.scrollTop = 0;
    	if (vm.step==1){
    		vm.step = 2;
    	} else {
    		if (dir=='prev') {
    			vm.step -= 1
    		} else {
    			vm.step +=1;
    		}
    	}
    },
    async createOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
      vm.$store.commit('LOADING', true);
        const isValid = await this.$refs.observer.validate();
        if (isValid) {
          this.$http.post(url, { data: order }).then((response) => {
            console.log('訂單已建立', response);
            if (response.data.success) {
              vm.step = 3;
            	vm.orderId = response.data.orderId;
              vm.$store.dispatch('getCart');
              // alert('訂單已建立!');
               this.$bus.$emit('message:push', response.data.message , 'success');
            } else {
              // alert('訂單建立失敗!')
               this.$bus.$emit('message:push', response.data.message , 'danger');
            }
            vm.getOrder();
           vm.$store.commit('LOADING', false);
          });
        } else {
          console.log('欄位不完整');
          this.$refs.observer.setErrors('欄位不完整');
          vm.$store.commit('LOADING', false);
        }
    },
    getOrder() {
			const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`; 
      this.$http.get(url).then((response) => {
      	vm.order = response.data.order;
      });
    },
		payOrder() {
			const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`; 
      this.$http.post(url).then((response) => {
      	if (response.data.success) {
      		vm.getOrder();
      	}
      });
    }
  },
  computed: {
    ...mapGetters(['cart', 'isLoading']),
    cartEmpty(){
      if (this.cart.carts.length==0){
        return true
      } else {
        return false
      }
    }
  },
	created() {
    this.getCart();
  },
}
</script>
<style scoped>
.delAll {
  cursor: pointer;
}

/*-----------------*/
/*step button
/*-----------------*/
.u-orderbtn-step{
	display: flex;
	justify-content: flex-end;
}
.u-orderbtn-shop{
	text-align: center;
	margin-top:36px;
}
@media (min-width:768px) {
	.u-orderbtn-shop{
		text-align: left;
		margin-top:0px;
	}
}
.img-thumbnail {
  max-width: 60px;
}
</style>


