<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="row mt-4">
      <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
        <div class="card border-0 shadow-sm">
          <div style="height: 200px; background-size: cover; background-position: center"
            :style="{backgroundImage: `url(${item.imageUrl})`}">
          </div>
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
            <h5 class="card-title">
              <a href="#" class="text-dark">{{ item.title }}</a>
            </h5>
            <p class="card-text">{{ item.content }}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h5" v-if="!item.price">{{ Number(item.origin_price) | toCurrency }} 元</div>
              <del class="h6" v-if="item.price">原價 {{ Number(item.origin_price) | toCurrency  }} 元</del>
              <div class="h5" v-if="item.price">現在只要 {{ Number(item.price) | toCurrency  }} 元</div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <button type="button" class="btn btn-outline-secondary btn-sm" @click.prevent="getProduct(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
              查看更多
            </button>
            <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click.prevent="addtoCart(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div><!--class="row mt-4"-->
    <!--購物車裡的品項-->
    <div class="row justify-content-md-center" v-if="cartList.total">
      <div class="col-md-8">
        <p class="h4">購物車列表</p>
        <table class="table" >
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">品名</th>
              <th class="text-right" scope="col">數量</th>
              <th class="text-right" scope="col">單價</th>
              <th class="text-right" scope="col">總價</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in cartList.carts" :key="index">
              <td class="align-middle">
                <button
                  class="btn btn-outline-danger btn-sm"
                  @click="delCartItem(item.id)"
                ><i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                {{ item.product.title }}
                <div class="text-success" v-if="item.coupon">
                  已套用優惠券 - {{ item.coupon.title}}
                </div>
              </td>
              <td  class="text-right">{{ item.qty }}</td>
              <td  class="text-right">{{ Number(item.product.price) | toCurrency }}</td>
              <td  class="text-right">{{ Number(item.final_total) | toCurrency }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="4" class="text-right">總計</td>
              <td class="text-right">{{ cartList.total | toCurrency }}</td>
            </tr>
            <tr v-if="cartList.final_total !== cartList.total">
              <td colspan="4" class="text-right text-success">折扣價</td>
              <td class="text-right text-success">{{ cartList.final_total | toCurrency }}</td>
            </tr>
          </tfoot>
        </table>
        <div class="input-group mb-3 input-group-sm">
          <input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼">
          <div class="input-group-append">
            <button class="btn btn-outline-secondary" type="button" @click="addCouponCode">
              套用優惠碼
            </button>
          </div>
        </div>
      </div>
    </div>
    <!--訂購人資料-->
    <div class="row justify-content-md-center">
      <p></p>
      <div class="col-md-6"><ValidationObserver ref="observer" tag="form" @submit.prevent="createOrder">
        <div class="form-group">
          <validation-provider rules="required|email" v-slot="{ errors }" name="email">
            <label for="useremail">Email</label>
            <input v-model="form.user.email" type="email" class="form-control" name="email" id="useremail" placeholder="請輸入 Email"/>
            <span class="text-danger">{{ errors[0] }}</span>
          </validation-provider>
        </div>
        <div class="form-group">
          <validation-provider rules="required" v-slot="{ errors }" name="name">
            <label for="username">收件人姓名</label>
            <input v-model="form.user.name" type="text" class="form-control" name="name" id="username" placeholder="請輸入 收件人姓名"/>
            <span class="text-danger">{{ errors[0] }}</span>
          </validation-provider>
        </div>
        <div class="form-group">
          <validation-provider rules="required" v-slot="{ errors }" name="tel">
            <label for="usertel">收件人電話</label>
            <input v-model="form.user.tel" type="text" class="form-control" name="tel" id="usertel" placeholder="請輸入 電話"/>
            <span class="text-danger">{{ errors[0] }}</span>
          </validation-provider>
        </div>
        <div class="form-group">
          <validation-provider rules="required" v-slot="{ errors }" name="address">
            <label for="useraddress">收件人地址</label>
            <input v-model="form.user.address" type="text" class="form-control" name="address" id="useraddress" placeholder="請輸入 收件地址"/>
            <span  class="text-danger">{{ errors[0] }}</span>
          </validation-provider>
        </div>
        <div class="form-group">
          <label for="useraddress">留言</label>
          <textarea name="" id="" class="form-control" cols="30" rows="10"
            v-model="form.message"></textarea>
        </div>
        <div class="text-right">
          <button class="btn btn-danger">送出訂單</button>
        </div>
      </ValidationObserver>
      </div>
    </div>
    <!--單筆產品的modal-->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" alt="">
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right"><small>{{ product.description }}</small></footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">{{ Number(product.origin_price) | toCurrency }} 元</div>
              <del class="h6" v-if="product.price">原價 {{ Number(product.origin_price) | toCurrency }} 元</del>
              <div class="h4" v-if="product.price">現在只要 {{ Number(product.price) | toCurrency }} 元</div>
            </div>
            <select name="" class="form-control mt-3" v-model="product.num">
              <option :value="num" v-for="num in 10" :key="num">
                選購 {{num}} {{product.unit}}
              </option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
              小計 <strong>{{ (product.num * product.price) | toCurrency }}</strong> 元
            </div>
            <button type="button" class="btn btn-primary"
              @click="addtoCart(product.id, product.num)">
              <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

import { ValidationProvider, extend, ValidationObserver } from 'vee-validate';
import * as rules from 'vee-validate/dist/rules';
import zh_TW from 'vee-validate/dist/locale/zh_TW';
// loop over all rules
for (let rule in rules) {
  extend(rule, {
    ...rules[rule], // add the rule
    message: zh_TW.messages[rule] // add its message
  });
}
export default {
  components: {
    ValidationProvider,
    ValidationObserver
  },
  data() {
    return {
      products: [],
      cartList: [],
      product: {},
      isLoading: false,
      status: {
        loadingItem: '',
      },
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },
      coupon_code: '',
    };
  },
  methods: {
    getProducts() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.products = response.data.products;
        vm.isLoading = false;
      });
    },
    getProduct(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      vm.status.loadingItem = id;
      this.$http.get(url).then((response) => {
        vm.product = response.data.product;
        // 開啟modal
        $('#productModal').modal('show');
        vm.status.loadingItem = '';
      });
    },
    addtoCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty,
      };
      this.$http.post(url, { data: cart }).then((response) => {
        console.log(response);
        vm.status.loadingItem = '';
        vm.getCart();
        $('#productModal').modal('hide');
      });
    },
    getCart() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.cartList = response.data.data;
        console.log(vm.cartList)
        vm.isLoading = false;
      });
    },
    delCartItem(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(url).then((response) => {
        vm.getCart();
        console.log(response)
        vm.isLoading = false;
      });    
    },
    addCouponCode() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
      const coupon = {
        code: vm.coupon_code,
      };
      vm.isLoading = true;
      this.$http.post(url, { data: coupon }).then((response) => {
        if (response.data.success) {
          console.log(response);
          this.$bus.$emit('message:push', response.data.message , 'success');
        } else {
          this.$bus.$emit('message:push', response.data.message , 'danger');
        }
          vm.getCart();
          vm.isLoading = false;
      });
    },
    async createOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
      vm.isLoading = true;
      // this.$validator.validate().then((result) => {
        const isValid = await this.$refs.observer.validate();
        if (isValid) {
          this.$http.post(url, { data: order }).then((response) => {
            console.log('訂單已建立', response);
            if (response.data.success) {
              vm.$router.push(`/customer_checkout/${response.data.orderId}`);
            }
            // vm.getCart();
            vm.isLoading = false;
          });
        } else {
          console.log('欄位不完整');
          this.$refs.observer.setErrors('欄位不完整');
          vm.isLoading = false;
        }
      // });
    },
  },
  created() {
    this.getProducts();
    this.getCart();
  },
};
</script>
<style scoped>

</style>



